<template>
  <div class="movie-view has-header">
    <scroller title="" type="hasCover" :items="hotMovies"></scroller>
    <scroller title="" type="hasCover" :items="topMovies"></scroller>
    <scroller title="" type="hasCover" :items="newMovies"></scroller>
    <scroller title="" type="onlyString" :items="movieTags"></scroller>
    <types></types>
    <download-app></download-app>
  </div>
</template>

<script>
import { mapState } from 'vuex'

import Scroller from '../components/Scroller.vue'
import Types from '../components/Types.vue'
import DownloadApp from '../components/DownloadApp.vue'

export default {
  name: 'movie-view',
  components: { Scroller, Types, DownloadApp },
  data () {
    return {}
  },
  computed: {
    ...mapState([
      'movieTags',
      'hotMovies',
      'newMovies',
      'topMovies'
    ])
  },
  methods: {
    getMovie: function () {
      this.$store.dispatch('getMovie')
    }
  },
  created () {
    this.getMovie()
  }
}
</script>

<style scoped>

</style>



